import React from "react";
import "./Benefit.scss";
import { TextComponent } from "../TextComponent";
import {benefitItem} from './benefitItem';
import { Lazy } from 'react-lazy';

const Benefit = ({id}) => {
  return (
    <div className="benefit" id={id}>
      <div className="container">
        <TextComponent judul="Apa keuntungan belajar bareng si tani?">
          Dengan gabung ke kelas gratis di si tani tentu aja ada banyak
          keuntunganya loh, kira-kira apa saja keuntungnya yuk simak!
        </TextComponent>
        <div className="benefitcard-wrapper" data-aos-anchor-placement="bottom-bottom" >
          {benefitItem.map((item) => {
            return (
              <div className="benefitcard" data-aos="fade-right">
                <Lazy ltIE9 className="img-wrapper">
                  <img src={item.img} alt="img-benefit" />
                </Lazy>
                <div className="text-wrapper">
                  <h5>{item.judul}</h5>
                  <p>{item.text}</p>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};
export default Benefit;